<template>
    <div class="BXcountdown">
		<div class="status-tag timeTitle" :class="statusType">
			{{ statusText }}
		</div>
      <div class="timer" v-if="statusType !== 'running' && statusType !== 'expired' && ShowTimer">
			<!-- <div class="day">
				<span class="number">{{ days }}</span>
				<div class="format">{{ wordString.day }}</div>
			</div>
			<div class="split">
				:
			</div> -->
         <div class="hour">
            <span class="number">{{ hours }}</span>
               <div class="format">{{ wordString.hours }}</div>
            </div>
         <div class="split">
            :
         </div>
         <div class="min">
            <span class="number">{{ minutes }}</span>
            <div class="format">{{ wordString.minutes }}</div>
         </div>
         <div class="split">
            :
         </div>
         <div class="sec">
            <span class="number">{{ seconds }}</span>
            <div class="format">{{ wordString.seconds }}</div>
         </div>
      </div>
		<div class="NoHaveSession" v-if="ShowTimer == false">
                <IcPlay height="50" width="40"/>
                <p>You Have No Session Today</p>
        </div>
        <div v-if="statusType === 'running' && statusType !== 'expired'" @click="startSession" class="IconNext">
        <!-- <div @click="startSession"> -->
          <lottie :options="defaultOptions" :height="110" :width="120" v-on:animCreated="handleAnimation"/>
        </div>
        <!-- <div class="message">{{ message }}</div> -->
        <!-- <div class="status-tag" :class="statusType">{{ statusText }}</div> -->

   </div>
</template>

<script>
	import IcPlay from '../../../../components/icons/SimulatorIcon.vue'
	import Lottie from '../../../../components/loading/Lottie.vue'
 	import * as animationData from '../../../../staticjson/animasi start sessionsf4fc1d925a1a68f7e5797266bcd9665cbc8c036508ce0b525c4e1aaba7d0ce88.json';
   import moment from 'moment'

	export default {
  props:[
    'startTime', 'endTime', 'trans', 'DataUpcoming'
  ],
	components: {
 		Lottie, IcPlay
	},
 	data () {
   	return {
       timer: "",
       wordString: {},
       start: "",
       end: "",
       interval: "",
       days: "",
       minutes: "",
       hours: "",
       seconds: "",
       message: "",
       statusType: "",
		   statusText: "",
		   ShowTimer:'',
		   //FOR LOTTIE
    	 showLottie:false,
    	 defaultOptions: {animationData: animationData},
    	 animationSpeed: 2
    }
  },
 	created () {
    this.wordString = JSON.parse(this.trans)
 	},
 	mounted () {
   	this.start = new Date(this.startTime).getTime()
   	this.end = new Date(this.endTime).getTime()
   	//Update the Count every 1 Second
   	this.timerCount(this.start, this.end)
   	this.interval = setInterval(() => {
       this.timerCount(this.start, this.end)
    }, 1000)
  },
  methods:{
   	timerCount(start, end){
       //Get todays date and time
       var now = new Date().getTime()
       //Find the distance between now and the count down date
       var distance = start - now
		   var passTime = end - now

            if (distance < 0 && passTime < 0){
               this.message = this.wordString.expired
               this.statusType = "expired"
               this.statusText = this.wordString.status.expired
               clearInterval(this.interval)
               
               setTimeout(() => {
                  window.location.reload()
               }, 1000);
               return
            }else if (distance < 0 && passTime > 0){
               this.calcTime(passTime)
               this.message = this.wordString.running
               this.statusType = "running"
               this.statusText = this.wordString.status.running
            }else if (distance > 0 && passTime > 0){
               this.calcTime(distance)
               this.message = this.wordString.upcoming
               this.statusType = "upcoming"
               this.statusType = this.wordString.status.upcoming
            }
         },
         calcTime(dist){
            // Time calculations for days, hours, minutes and seconds
            this.days = Math.floor(dist / (1000 * 60 * 60 * 24));
            this.hours = Math.floor((dist % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
            this.minutes = Math.floor((dist % (1000 * 60 * 60)) / (1000 * 60));
            this.seconds = Math.floor((dist % (1000 * 60)) / 1000);
         },
         //FOR LOTTIE
         handleAnimation: function (anim) {
            this.anim = anim;
         },
         stop: function () {
            this.anim.stop();
         },
         play: function () {
            this.anim.play();
         },
         pause: function () {
            this.anim.pause();
         },
         onSpeedChange: function () {
            this.anim.setSpeed(this.animationSpeed);
         },
         startSession () {
            const CurrentTime = moment().format("HH:mm:ss");
            const dataStartSession = {
               appointment_id: this.DataUpcoming[0].appointment_id,
               student_attend: CurrentTime,
               type: this.DataUpcoming[0].type
            }
            console.log(dataStartSession);

            this.$store.dispatch("POST_START_SESSION_STUDENT", dataStartSession)
         }
      },
      watch: {
         hours(val){
            if (val < '6' && this.days == '0'){
                  this.ShowTimer = true
               //console.log(this.ShowTimer)
            }else{
                  this.ShowTimer = false
               //console.log(this.ShowTimer)
            }
         }
      },
   }
</script>

<style lang="scss">
   .BXcountdown{
		width: 100%;
		.status-tag{
			text-align: center;
      }
		.timer{
			display: flex;
			justify-content: space-around;
			// background-color:$bgDarkGrey;
			border-radius: 4px;
			padding:10px;
			.split{
				font-size: 1.5em;
				font-weight: 600;
				color:#375197;
			}
			.day, .hour, .min, .sec{
				text-align: center;
				.format{
					font-size: 0.8em;
					color:#375197;
				}
				.number{
					color:#375197;
					font-size: 1.5em;
					font-weight: 600;
				}
			}
      }
   }
   .IconNext{
      div{
         cursor: pointer !important;
      }
   }
</style>



