<template>
    <div class="bxTokens">
      <div class="view__header">
        <div class="view__header__title">
          <div>
            <h3>Token</h3>
          </div>
        </div>
      </div>
        <div class="bxTokens__bxCurrentToken">
            <div class="bxTokens__bxCurrentToken__TokenInfo">
                <p>TOKEN BALANCE</p>
                <p>{{this.GET_TokenInfo.data.token_amount}}</p>
                <router-link to="/student/token">
                  <v-btn class="btnRound text-capitalize BackGradient">Request More Token</v-btn>
                </router-link>
            </div>
            <div class="bxTokens__bxCurrentToken__TokenHistory">
                <div class="recTitle">Recent Transactions</div>
                <div class="History" v-for="(n, index) in this.DataTokenHistory.slice(0,4)" :key="index">
                    <div class="dateTitle">
                        <p>{{n.created_at | dateFormat}}</p>
                        <p>Sessions with {{n.coach_name}}</p>
                    </div>
                    <div class="tokenDetail">
                        <p>-{{n.token_amount}} Token</p>
                        <p>{{n.balance}}</p>
                    </div>
                </div>
            </div>
            <!-- <v-btn class="btnMore text-capitalize">Show More</v-btn> -->
        </div>
    </div>
</template>

<script>
   import moment from 'moment'
   export default {
      props: [
        'GET_TokenInfo',
        'DataTokenHistory'
      ],
      filters: {
      dateFormat: function (value) {
         if (value) {
            return moment.utc(value).local().format('MMMM, Do YYYY HH:mm:ss')
         }
      },
      
   }

   }
</script>

<style lang="scss">

</style>



