<template>
    <div class="BXupcomingSessions">
      <div class="view__header">
        <div class="view__header__title">
          <div>
            <h3>Upcoming Sessions</h3>
          </div>
        </div>
      </div>
        <v-layout class="ContainUp" align-center justify-space-between>
            <div class="BXupcomingSessions__bxInfoSessions">
                <div class="BXupcomingSessions__bxInfoSessions__NoUpcomming" v-if="this.DataUpcoming.length == 0">
                   You Have No Upcoming Sessions Today
                </div>
                <div 
                    class="BXupcomingSessions__bxInfoSessions__UpcomingInfo"
                    v-for="(n, index) in this.GET_DataUpcoming.slice(0,4)" :key="index"
                >
                    <div class="BXupcomingSessions__bxInfoSessions__UpcomingInfo--Upcoming">
                        <div class="Upicon">
                            <v-icon medium>event</v-icon>
                        </div>
                        <div class="UpDateTime">
                            <div class="UpDateTime__Date">
                                {{n.date | dateFormat}}
                            </div>
                            <div class="UpDateTime__Time">
                                {{n.start_time}} (UTC {{n.date | dateFormatUtc}})
                            </div>
                        </div>
                        <div class="UpWith">
                             <p>with</p>
                             <p>{{n.coach_name}}</p>
                             <p><router-link to="/student/view">{{n.coach_name}}</router-link></p>
                        </div>
                        <div @click="Get_Username(n.username)" class="UpInfo">  
                            <router-link  to="/student/view">
                                INFO
                            </router-link>
                        </div>
                       

                    </div>
                </div>
                <!-- <div class="BXupcomingSessions__bxInfoSessions__BxInfo"  v-for="(n, index) in this.GET_DataUpcoming" :key="index">
                    <div class="BXupcomingSessions__bxInfoSessions__BxInfo__info">
                        <v-avatar size="40">
                            <img :src="n.avatar">
                        </v-avatar>
                        <div>
                            <p>Session with </p><p @click="Get_Username(n.username)"> <router-link to="/student/view">{{n.coach_name}}</router-link></p>
                        </div>
                    </div>
                    <div class="BXupcomingSessions__bxInfoSessions__BxInfo__bxInfoTime">
                        <p>{{n.date | dateFormat}}</p>
                        <p>{{n.start_time}} (UTC {{n.date | dateFormatUtc}})</p>
                    </div>
                </div> -->

            </div>
        </v-layout>

    </div>
</template>

<script>
    import moment from 'moment'
    export default{
        props:[
            'DataUpcoming'
        ],
        data () {
            return{
                
            }
        },
        computed:{
            GET_DataUpcoming () {
                return this.DataUpcoming
            },
            
        },
        methods: {
            Get_Username(username){
                this.$store.commit('muttUpcomingSelect',{
                    Username : username
                })
            }
        },
        filters: {
            dateFormat: function (value) {
                if (value) {
                    return moment(String(value)).format('ddd, D MMMM YYYY')
                }
            },
            dateFormatUtc: function (value) {
                if (value) {
                    var newdate = moment.utc(value).local().format('Z')
                    return newdate
                }
            }
        }
    }
</script>

<style lang="scss">

</style>



