<template>
  <v-app class="Bxdashboard">
    <div class="dashoboard__loader" v-if="!myData">
       <content-loader
          v-if="!myData"
          v-show="ResizeScreen == 1"
          :height="600"
          :width="350"
          :speed="2"
          primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
       >
       <rect x="20" y="0" rx="0" ry="0" width="108.57" height="20" />
       <rect x="20" y="30" rx="0" ry="0" width="310" height="100" />
       <rect x="20" y="150" rx="0" ry="0" width="310" height="100" />
       <rect x="20" y="270" rx="0" ry="0" width="310" height="100" />
       <rect x="20" y="410" rx="0" ry="0" width="108.57" height="20" />
       <rect x="20" y="440" rx="0" ry="0" width="310" height="120" />
       </content-loader>

       <content-loader
          v-if="!myData"
          v-show="ResizeScreen == 2"
          :height="600"
          :width="400"
          :speed="2"
          primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
       >
       <rect x="20" y="0" rx="0" ry="0" width="108.57" height="20" />
       <rect x="20" y="30" rx="0" ry="0" width="150" height="100" />
       <rect x="190" y="30" rx="0" ry="0" width="190" height="230" />
       <rect x="20" y="140" rx="0" ry="0" width="150" height="50" />
       <rect x="20" y="200" rx="0" ry="0" width="150" height="60" />
       <rect x="20" y="300" rx="0" ry="0" width="108.57" height="20" />
       <rect x="20" y="330" rx="0" ry="0" width="360" height="140" />
       </content-loader>

       <content-loader
          v-if="!myData"
          v-show="ResizeScreen == 3"
          :height="285"
          :width="500"
          :speed="2"
          primaryColor="#f3f3f3"
				secondaryColor="#ecebeb"
       >
       <rect x="4.63" y="74.61" rx="0" ry="0" width="108.57" height="19" />
       <rect x="4.63" y="31.61" rx="0" ry="0" width="108.57" height="39.9" />
       <rect x="286.63" y="44.61" rx="0" ry="0" width="0" height="0" />
       <rect x="4.63" y="96.61" rx="0" ry="0" width="108.57" height="66.5" />
       <rect x="129.63" y="31.61" rx="0" ry="0" width="219.6" height="193.2" />
       <rect x="369.63" y="32.61" rx="0" ry="0" width="127" height="175.48" />
       <rect x="416.63" y="216.61" rx="0" ry="0" width="0" height="0" />
       <rect x="4.63" y="20.61" rx="0" ry="0" width="56.46" height="8.55" />
       <rect x="129.63" y="20.61" rx="0" ry="0" width="56.46" height="8.55" />
       <rect x="369.63" y="20.61" rx="0" ry="0" width="56.46" height="8.55" />
       </content-loader>

    </div>
    <v-layout class="Bxdashboard__menu">
       <Menu v-if="myData"/>
    </v-layout>
    <div>
       <v-layout class="Bxdashboard__content" v-if="myData">
          <div class="Bxdashboard__content__NextSessions">
             <NextSession :DataUpcoming ='DataUpcoming'/>
          </div>
          <div class="Bxdashboard__content__bxUpSessions">
             <UpcomingSessions :DataUpcoming ='DataUpcoming'/>
          </div>
          <div class="Bxdashboard__content__bxToken">
             <Token :GET_TokenInfo="GET_TokenInfo" :DataTokenHistory="DataTokenHistory"/>
          </div>
       </v-layout>
    </div>
 </v-app>
</template>

<script>
import Pusher from 'pusher-js';
import { ContentLoader } from 'vue-content-loader';
import Menu from '../../student-components/dashboard/menu/Menu'
import NextSession from '../../student-components/dashboard/nextSession/NextSession'
import UpcomingSessions from '../../student-components/dashboard/upcomingSessions/UpcomingSessions'
import Token from '../../student-components/dashboard/token/Token'

import moment from 'moment'



export default {
   components: {
      Menu,
      NextSession,
      UpcomingSessions,
      Token,
      ContentLoader,
   },

   props: {
      source: String
   },

   data () {
      return {
         drawer: null,
         myData: null,
         windowWidth:0,
         windowHeigt:0,
         ResizeScreen: null
      }
   },

   created() {
      this.TrigDispatch()
      this.$store.dispatch("GET_SELECTFILTER");
   },

   mounted() {

      this.$nextTick( () => {
         window.addEventListener('resize', this.getWindowWidth);
         window.addEventListener('resize', this.getWindowHeight);
         this.getWindowWidth()
         this.getWindowHeight()

         setTimeout(() => {
            this.CheckLoad
            this.TrigDispatch()
         }, 1000);
      })
   },

   methods: {
      getWindowWidth(event){
         this.windowWidth = document.documentElement.clientWidth;

         if (this.windowWidth <= 599){
         //this for resolution phone only
         this.ResizeScreen = 1
         }else if(this.windowWidth <= 768){
            //this for resolution Ipad Potrait
         this.ResizeScreen = 2
         }else if(this.windowWidth >= 1024){
            //this for resolution Dekstop-up
         this.ResizeScreen = 3
         }
      },

      getWindowHeight(event){
         this.windowHeigt = document.documentElement.clientHeight;
      },

      TrigDispatch(){
         this.$store.dispatch("GET_DASH_UPCOMING_SESSIONS");
         this.$store.dispatch("GET_PROFILE_TOKEN");
         this.$store.dispatch("GET_PROFILE_TOKEN_HISTORY");
      }
   },
  
   computed: {
      StoreDataUpcoming(){
         return this.$store.state.DashUpcomingSessions
      },
      storeDataTokenHistory(){
         return this.$store.state.DashTokenHistory
      },
      DataUpcoming () {
         const items = []
         const dataValue = this.StoreDataUpcoming.new_appointment

         dataValue.forEach(function (value, key) {
            moment(value.date).format('MMMM, Do YYYY')
            items.push(dataValue);
            // console.log(key +" " + value.date+ " " + value.start_time);
         });

         function compare(a, b){
            if (a.date < b.date) return -1;
            if (a.date > b.date) return 1;
            if (a.start_time < b.start_time) return -1;
            if (a.start_time > b.start_time) return 1;
         }
         function compare2(a, b){
            if (a.start_time < b.start_time)
               return -1;
            if (a.start_time > b.start_time)
               return 1;
            return 0;
         }
         return [...dataValue].sort(compare)
      },
      DataTokenHistory(){
         const items = []
         const dataValue = this.storeDataTokenHistory.data

         dataValue.forEach(function (value, key) {
            items.push(dataValue);
            // console.log(key +" " + value.created_at+ " " + value.token_amount);
         });

         function compare(a, b){
            if (a.created_at > b.created_at) return -1;
            if (a.created_at < b.created_at) return 1;
         }
         return [...dataValue].sort(compare)
      },
      GET_TokenInfo(){
         return this.$store.state.TokenProfile
      },

      CheckLoad(){
         if (this.$store.getters.CCK_DASHBOARD !== 'IS NULL'){
            const ItemsCont = []
            for (var i in this.$store.getters.CCK_DASHBOARD){
               ItemsCont.push(this.$store.getters.CCK_DASHBOARD[i])
            }
            return ItemsCont
         }else{
            return 'IS NULL'
         }
      }
   },

   watch: {
      CheckLoad(val){
         if (val === 'IS NULL'){
            this.TrigDispatch()
            setTimeout(() => {
               if (val === 'IS NULL'){
                  this.$store.commit('MuttAllertCont', {
                     Alert : 'yes',
                     TextAlert : 'Something wrong, Please Reload your Browser'
                  })
                  this.myData = null
                  console.log(val)
               }
            }, 1000);
         
         }else{
            const DataUpcoming = val[0]
            const HistoryCoin = val[1]
            const CoinProfile = val[2]

            if (DataUpcoming == 200 && HistoryCoin == 200 && CoinProfile == 200){
               this.myData = 'Success'
               this.$store.commit('MuttAllertCont', {
                  Alert : 'no',
                  TextAlert : 'Success'
               })
            }else if(DataUpcoming == 400 || HistoryCoin == 400 || CoinProfile == 400){
               this.myData = null
               this.$store.commit('MuttAllertCont', {
                  Alert : 'yes',
                  TextAlert : 'Bad Request, Please try again in a few minutes'
               })
            }else if(DataUpcoming == 500 || HistoryCoin == 500 || CoinProfile == 500){
               this.myData = null
               this.$store.commit('MuttAllertCont', {
                  Alert : 'yes',
                  TextAlert : 'Server Internal Error, Please try again in a few minutes'
               })
            }else if(DataUpcoming == 401 || HistoryCoin == 401 || CoinProfile == 401){
               this.myData = null
               this.$store.commit('MuttAllertCont', {
                  Alert : 'yes',
                  TextAlert : 'Unauthorized, Please try again in a few minutes'
               })
            }
         }
      }
   },

   beforeDestroy() {
      window.removeEventListener('resize', this.getWindowWidth);
      window.removeEventListener('resize', this.getWindowHeight);
   },
   destroyed() {
      this.$store.commit('MuttAllertCont', {
         Alert : 'no',
         TextAlert : 'exit'
      })
   },
   
}
</script>

<style lang="scss">
    @import '../../styles/Dashboard.scss';
</style>



