<template>
    <div class="BXnextSessions">
      <div class="view__header">
        <div class="view__header__title">
          <div>
            <h3>Next Session</h3>
          </div>
        </div>
      </div>
        <div class="BXnextSessions__BXcountdown" v-if="dateStartTime !== ''">
            <div class="BXnextSessions__BXcountdown__DateTime">
                <span class="BXnextSessions__BXcountdown__DateTime__date">{{date | dateFormat}}</span>
                <div class="BXnextSessions__BXcountdown__DateTime__time">
                    <p>{{times}}</p>
                </div>
            </div>
        </div>
         <!-- <div class="BXnextSessions__BXcountdown" v-if="dateStartTime == '' ">
            <div class="NoHaveSession">
                <IcPlay height="50" width="40"/>
                <p>You Have No Session Today</p>
            </div>
         </div> -->

        <div class="BXnextSessions__BXcountdown">
            <!-- <span class="timeTitle">Session Start In</span> -->

            <Countdown
                :startTime = dateStartTime
                :endTime = dateEndTime
                :DataUpcoming = DataUpcoming
                trans='{
                    "day":"Day",
                    "hours":"Hours",
                    "minutes":"Minuets",
                    "seconds":"Seconds",
                    "expired":"Event has been expired.",
                    "running":"Till the end of event.",
                    "upcoming":"Till start of event.",
                    "status": {
                        "expired":"Session Expired",
                        "running":"Session is Running",
                        "upcoming":"Upcoming Session"
                    }
                }'
            />

        </div>
        <div class="BXnextSessions__BxSimulator">
            <ICsimulator
              width="50px"
              height="80px"
              color="#0070B5" />
            <div class="TextSimulator">
                <p>Make sure to check your<br>network and configuration with<br>simulator before runnings sessions</p>
                <span class="btnGrayscle BackGradient" @click="getSessionSimulator">Session Simulator</span>
            </div>
        </div>
    </div>
</template>

<script>
    import ICsimulator from '../../../components/icons/CheckSimulatorIcon.vue'
    import IcPlay from '../../../components/icons/SimulatorIcon.vue'
    import Countdown from './countdown/Countdown.vue'
    import moment from 'moment'
    export default{
        props:[
            'DataUpcoming'
        ],
        components:{
            ICsimulator, Countdown, IcPlay
        },
        data () {
            return {
                date: '',
                times: "",
                dateStartTime:'',
                dateEndTime:'',
                datetimestart:'march 22, 2019 16:30:00',
                datetimeend: 'march 22, 2019 17:00:00'
            }
        },
        filters: {
            formatDate(value) {
                return moment(value).format('hh:mm A').slice(5)
            },
            dateFormat(value) {
                if (value) {
                    return moment(String(value)).format('MMMM, Do YYYY')
                }
            }
        },
        computed: {
            GET_DataUpcoming(){
                return this.DataUpcoming[0]
            },
            FormatStartTime () {
                return this.GET_DataUpcoming.date ? moment(this.GET_DataUpcoming.date).format('MMMM D, YYYY') : ''
            },
            FormatEndTime () {
                return this.GET_DataUpcoming.date ? moment(this.GET_DataUpcoming.date).format('MMMM D, YYYY') : ''
            },
        },

        created() {
            this.date = this.GET_DataUpcoming ? this.GET_DataUpcoming.date : null;
            this.times = this.GET_DataUpcoming ?  this.GET_DataUpcoming.start_time.slice(0,5) :null;
            this.dateStartTime = this.GET_DataUpcoming ? this.FormatStartTime + " " + this.GET_DataUpcoming.start_time : '';
            this.dateEndTime = this.GET_DataUpcoming ? this.FormatEndTime + " " + this.GET_DataUpcoming.end_time :null;
        },
        methods: {
          getSessionSimulator () {
            this.$store.dispatch("GET_SESSION_SIMULATOR")
          },
        }
    }
</script>

<style lang="scss">

</style>



